import React, { useRef, useState } from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import "swiper/css/effect-coverflow"; //     CSS
import "swiper/css/navigation"; //     
import "swiper/css/pagination"; //     
import { EffectCoverflow, Pagination, Autoplay } from "swiper/modules";

function DetailImgSlide({ styles, projectImg }) {
  const [activeIndex, setActiveIndex] = useState();
  const swiperRef = useRef(null);
  return (
    <div className={styles.swiperContainer}>
      <Swiper
        effect={"coverflow"}
        grabCursor={true}
        centeredSlides={true}
        loop={true}
        slidesPerView={1}
        autoplay={{ delay: 2500, disableOnInteraction: false }}
        coverflowEffect={{
          rotate: 0,
          stretch: 10,
          depth: 200,
          modifier: 2,
        }}
        pagination={{
          el: `.${styles.swiperPagination}`,
          clickable: true,
          type: "custom",
        }}
        onSwiper={(swiper) => (swiperRef.current = swiper)} // Swiper  
        onSlideChange={(swiper) => setActiveIndex(swiper.realIndex)} //     
        modules={[EffectCoverflow, Pagination, Autoplay]}
        breakpoints={{
          1080: {
            slidesPerView: 2,
          },
        }}
        className={styles.swiper}
      >
        {projectImg &&
          projectImg.map((img, i) => (
            <SwiperSlide
              key={i}
              className={styles.swiperSlide}
              style={{ width: "fit-content" }}
            >
              <img src={img.img} alt={img.alt} />
            </SwiperSlide>
          ))}
      </Swiper>
      <div className={styles.swiperPagination}>
        {projectImg &&
          projectImg.map((_, index) => (
            <span
              key={index}
              className={`${styles.paginationBullet} ${
                index === activeIndex ? styles.active : ""
              }`}
              onClick={
                () => {
                  setActiveIndex(index);
                  swiperRef.current.slideToLoop(index);
                } // Swiper  
              }
            ></span>
          ))}
      </div>
    </div>
  );
}

export default DetailImgSlide;
